import React, { Component } from 'react'
import { Grid, Cell } from 'react-mdl'
import Qualification from './resume_qual';
import Skills from './resume_skills';

export default class Resume extends Component {
    render() {
        return (
            <div>
                <Grid>
                    <Cell col={4}>
                        <div style={{ textAlign: 'center' }}>
                            <img src="https://png.pngtree.com/element_origin_min_pic/17/07/06/bb87c06b8cb8fad5ffe9abab11679fae.jpg" alt="avatar" style={{ height: '200px', borderRadius: '50%' }} />
                        </div>
                        <h2 style={{ paddingTop: '2em' }}>Ujjal Acharya</h2>
                        <h4 style={{ color: 'grey' }}>Programmer</h4>
                        <hr style={{ borderTop: '3px solid #833fb2', width: '50%' }} />
                        <p>Ujjal Acharya is a student pursuing BscCSIT at Madan Bhandari Memorial College, Bijulibajar-Kathmandu. He is a tech enthusiast who is passionate about programming and machines. He's a green thinker, science lover, aesthetic voyager, problems, puzzles admirer. He is very interested in the intersection of media, entertainment, and technology and is focused on building software that creates immersive experiences for people.</p>
                        <hr style={{ borderTop: '3px solid #833fb2', width: '50%' }} />
                        <h5>Address</h5>
                        <p>New Baneswor, Kathamandu</p>
                        <h5>Phone</h5>
                        <p>9848658331</p>
                        <h5>Email</h5>
                        <p>acharyaujjal1@gmail.com</p>
                        <hr style={{ borderTop: '3px solid #833fb2', width: '50%' }} />

                    </Cell>
                    <Cell className="resume-right-col" col={8}>
                        <h2>Education</h2>
                        <Qualification
                            endYear="2012"
                            name="Stepping Stone English School"
                            desc="Computer, Add. Math"
                        />
                        <Qualification
                            startYear="2013"
                            endYear="2014"
                            name="KMC"
                            desc="Bio, Add. Math"
                        />
                        <Qualification
                            startYear="2015"
                            endYear="2019"
                            name="Madan Bhandari Memorial College"
                            desc="CSIT- The B.Sc.CSIT curriculum is designed by closely following the course practiced in accredited international universities and institutions, subject to the condition that the intake students are mostly from twelve year of schooling in the science stream or equivalent from any university recognized by Tribhuvan University."
                        />
                        <hr style={{ borderTop: '3px solid #e22947' }} />
                        <h2>Experience</h2>
                        <Qualification
                            startYear="2016"
                            endYear="Summer Intern"
                            name="KrishiGhar.com"
                            desc="Assisted as needed in the programming department. Programmed and tested codes. Edited text for articles, whitepapers and website."
                        />
                        <hr style={{ borderTop: '3px solid #e22947' }} />
                        <h2>Skills</h2>
                        <Skills
                            name="JavaScript"
                            progress={85}
                        />
                        <Skills
                            name="HTML/CSS"
                            progress={60}
                        />
                        <Skills
                            name="ReactJS"
                            progress={70}
                        />
                        <Skills
                            name="NODEJS"
                            progress={90}
                        />
                        <Skills
                            name="React Native"
                            progress={45}
                        />
                    </Cell>
                </Grid>
            </div>
        )
    }
}



