import React, { Component } from 'react'
import { Tabs, Tab, Card, CardTitle, CardActions, Button, CardText } from 'react-mdl';

class Projects extends Component {
    state = {
        activeTab: 0
    }
    showcard = () => {
        if (this.state.activeTab === 0) {
            return (
                <div className="card-content">
                    <Card shadow={0} style={{ width: '320px', height: '320px', margin: 'auto' }}>
                    <CardTitle expand style={{color: '#fff', background: 'url(https://cdn-images-1.medium.com/max/512/1*6kK9j74vyOmXYm1gN6ARhQ.png) center 10% #46B6AC' }}>Youtubeclone</CardTitle>
                    <CardText>
                       A youtubeclone made from reactJS that is blazingly fast than youtube itself      
                    </CardText>
                    <CardActions border>
                        <Button colored><a href="https://github.com/ujjalacharya/react-youtubeclone">Youtubeclone</a></Button>
                    </CardActions>
                </Card>
                <Card shadow={0} style={{ width: '320px', height: '320px', margin: 'auto' }}>
                    <CardTitle expand style={{ color: '#fff', background: 'url(https://cdn-images-1.medium.com/max/512/1*6kK9j74vyOmXYm1gN6ARhQ.png)  center 10% #46B6AC' }}>UMDB</CardTitle>
                    <CardText>
                        An IMDB clone made from ReactJS
                    </CardText>
                    <CardActions border>
                        <Button colored><a href="https://github.com/ujjalacharya/umdb">UMDB</a></Button>
                    </CardActions>
                </Card>
              
                <Card shadow={0} style={{ width: '320px', height: '320px', margin: 'auto' }}>
                    <CardTitle expand style={{ color: '#fff', background: 'url(https://cdn-images-1.medium.com/max/512/1*6kK9j74vyOmXYm1gN6ARhQ.png)  center 10% #46B6AC' }}>MBM Friends</CardTitle>
                    <CardText>
                        A simple React App containing info of college friends
                    </CardText>
                    <CardActions border>
                        <Button colored><a href="https://github.com/ujjalacharya/mbm_mates">MBM Friends</a></Button>
                    </CardActions>
                </Card>
                </div>
               
            )
        }
        if (this.state.activeTab === 1) {
            return (
                <div className="card-content">
                <Card shadow={0} style={{ width: '320px', height: '320px', margin: 'auto' }}>
                <CardTitle expand style={{color: '#fff', background: 'url(https://ih1.redbubble.net/image.109336634.1604/flat,550x550,075,f.u1.jpg) center 50% #46B6AC' }}>GoalNotes</CardTitle>
                <CardText>
                   An app where you can login and store you goals      
                </CardText>
                <CardActions border>
                    <Button colored><a href="https://github.com/ujjalacharya/goalnote">GoalNotes</a></Button>
                </CardActions>
            </Card>
                <Card shadow={0} style={{ width: '320px', height: '320px', margin: 'auto' }}>
                <CardTitle expand style={{color: '#fff', background: 'url(https://ih1.redbubble.net/image.109336634.1604/flat,550x550,075,f.u1.jpg) center 50% #46B6AC' }}>Chat App</CardTitle>
                <CardText>
                  A chat app made from NODEJS      
                </CardText>
                <CardActions border>
                    <Button colored><a href="https://github.com/ujjalacharya/node-chatapp">Chat App</a></Button>
                </CardActions>
            </Card>
                <Card shadow={0} style={{ width: '320px', height: '320px', margin: 'auto' }}>
                <CardTitle expand style={{color: '#fff', background: 'url(https://ih1.redbubble.net/image.109336634.1604/flat,550x550,075,f.u1.jpg) center 50% #46B6AC' }}>LMS</CardTitle>
                <CardText>
                  A system with which you can manage your staff info (UNDERCONSTRUCTIOIN)      
                </CardText>
                <CardActions border>
                    <Button colored><a href="https://github.com/ujjalacharya/leavemgtsys">LMS</a></Button>
                </CardActions>
            </Card>
            
            </div>
            )
        }
        if (this.state.activeTab === 2) {
            return (
                <div>Tab 2</div>
            )
        }
    }
    render() {
        return (
            <div>
                <Tabs activeTab={this.state.activeTab} onChange={(tabId) => this.setState({ activeTab: tabId })} ripple>
                    <Tab>React</Tab>
                    <Tab>NODEJS</Tab>
                    <Tab>React-Native</Tab>
                </Tabs>
                <section>
                    {this.showcard()}
                </section>
            </div>
        );
    }
}

export default Projects;



