import React, { Component } from 'react'
import { Grid, Cell, List, ListItem, ListItemContent } from 'react-mdl';

export default class Contact extends Component {
    render() {
        return (
            <div className="contact-body">
                <Grid className="contact-grid">
                    <Cell col={6}>
                        <h2>Ujjal Acharya</h2>
                        <img style={{ height: '250px', borderRadius: '5%' }} src="https://yt3.ggpht.com/a-/AJLlDp28Qu5BYlEfCDMx4o4F_l9DJ4Ga7CUCl0dX_A=s900-mo-c-c0xffffffff-rj-k-no" alt="avatar" />
                        <p style={{ paddingTop: '10px', width: '75%', margin: 'auto' }}>A tech enthusiast student who is passionate about
                        programming and machines. A green thinker, science lover,
                        aesthetic voyager, problems, puzzles admirer
</p>
                    </Cell>
                    <Cell col={6}>
                        <h2>Contact Me</h2>
                        <hr />
                        <div className="contact-list">
                            <List>
                                <ListItem>
                                    <ListItemContent style={{ fontSize: '40px', fontFamily: 'Anton, sans-serif', marginLeft: '30px' }}>
                                        <i className="fa fa-phone-square"></i>
                                        +977 9848658331</ListItemContent>
                                </ListItem>
                                <ListItem>
                                    <ListItemContent style={{ fontSize: '40px', fontFamily: 'Anton, sans-serif', marginLeft: '30px' }}>
                                        <i className="fa fa-envelope"></i>
                                        acharyaujjal1@gmail.com</ListItemContent>
                                </ListItem>
                                <ListItem>
                                    <ListItemContent style={{ fontSize: '40px', fontFamily: 'Anton, sans-serif', marginLeft: '30px' }}>
                                        <i className="fa fa-skype"></i>
                                        ujjalacharya</ListItemContent>
                                </ListItem>

                            </List>
                        </div>

                    </Cell>
                </Grid>
            </div>
        )
    }
}



